import React from 'react'
import { API } from '../../backend'
// import Base from '../Base'

const ImageHelper = ({product}) => {

    const imageUrl = product ? `${API}/product/photo/${product._id}` : 'data:image/jpeg;base64,'
    return (
       <img
       src={imageUrl}
       alt="photo"
       width="400"
       height="275"
       ></img>
    )
}
export default ImageHelper